import React from "react";
import theme from "theme";
import { Theme, Link, Strong, Text, LinkBox, Box, Section, Span } from "@quarkly/widgets";
import { Helmet } from "react-helmet";
import { GlobalQuarklyPageStyles } from "global-page-styles";
import { RawHtml, Override } from "@quarkly/components";
import * as Components from "components";
import { FiMenu } from "react-icons/fi";
import { AiOutlineMenu } from "react-icons/ai";
import { useState, useEffect } from 'react';
import { initializeApp } from "firebase/app";
import { getDatabase, ref, get } from 'firebase/database';


export default (() => {
	const [complaints, setComplaints] = useState([]);

	const firebaseConfig = {
		apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
		authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
		databaseURL: process.env.REACT_APP_FIREBASE_DATABASE_URL,
		projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
		storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
		messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
		appId: process.env.REACT_APP_FIREBASE_APP_ID,
	};

	useEffect(() => {
		const fetchComplaints = async () => {
		  try {
			const app = initializeApp(firebaseConfig);
			const db = getDatabase(app);
			const complaintRef = ref(db, 'complaints');
			const snapshot = await get(complaintRef);
	
			if (snapshot.exists()) {
			  const complaintList = Object.entries(snapshot.val()).map(([key, value]) => ({
				id: key,
				...value
			  }));
	
			  setComplaints(complaintList.reverse());
	
			  sessionStorage.setItem('complaints', JSON.stringify(complaintList));
			}
		  } catch (error) {
			return;
		  }
		};
	
		fetchComplaints();
	  }, []);

	return <Theme theme={theme}>
		<GlobalQuarklyPageStyles pageUrl={"index"} />
		<Helmet>
			<title>Project Vigil 1602</title>
			<link rel={"shortcut icon"} href={"https://uploads.quarkly.io/65e8b65d926d910020edc60b/images/protection.png?v=2024-08-22T13:21:07.100Z"} type={"image/x-icon"} />
		</Helmet>
		<Section sm-padding="8px 0 8px 0" quarkly-title="Header-5">
			<Override slot="SectionContent" flex-direction="row" justify-content="space-between" />
			<Box
				display="flex"
				padding="12px 0"
				justify-content="space-between"
				align-items="flex-start"
				flex-direction="row"
				width="30%"
				sm-width="50%"
				sm-align-items="center"
				sm-flex-direction="row"
				sm-justify-content="flex-start"
				md-width="50%"
				lg-width="70%"
				md-justify-content="flex-start"
			>
				<LinkBox flex-direction="row" href="/index" display="flex" grid-gap="12px">
					<Text
						margin="0"
						md-margin="0px 0 0px 0"
						text-align="left"
						font="normal 400 30px/1.5 --fontFamily-sans"
						sm-margin="0px 0 0px 0"
						display="block"
					>
						<Strong>
							Project Vigil 1602
						</Strong>
					</Text>
				</LinkBox>
			</Box>
			<Components.QuarklycommunityKitMobileSidePanel
				menuPosition="full"
				breakpoint="lg"
				width="70%"
				sm-width="50%"
				md-width="50%"
				lg-width="30%"
			>
				<Override slot="Children" md-display="flex" />
				<Override
					slot="Content"
					padding="0px 0px 0px 0px"
					background="rgba(255, 255, 255, 0)"
					lg-background="#ffffff"
					lg-margin="0px 0px 0px 0px"
				/>
				<Override
					slot="Button Text"
					font="normal 600 16px/1.5 -apple-system, system-ui, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif"
					text-transform="uppercase"
					letter-spacing="1px"
					sm-font="normal 600 14px/1.5 -apple-system, system-ui, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif"
					sm-margin="0px 2px 0px 0px"
					lg-margin="0px 0px 0px 0px"
				/>
				<Override
					slot="Button Icon :closed"
					category="fi"
					icon={FiMenu}
					size="32px"
					padding="5px 7px 5px 7px"
					border-radius="50px"
				/>
				<Override
					slot="Button Icon"
					width="28px"
					height="28px"
					category="ai"
					icon={AiOutlineMenu}
					color="--dark"
					size="24px"
					lg-width="32px"
					lg-height="32px"
				/>
				<Override
					slot="Cross"
					lg-width="32px"
					lg-height="32px"
					size="32px"
					top="24px"
					right="24px"
				/>
				<Box
					align-items="center"
					lg-justify-content="center"
					lg-align-items="flex-start"
					justify-content="flex-end"
					display="flex"
					lg-flex-direction="column"
					lg-margin="0px auto 0px auto"
					lg-min-width="300px"
					lg-max-width="1280px"
					lg-width="90%"
					lg-padding="24px 0px 48px 0px"
				>
					<Components.QuarklycommunityKitMenu
						display="flex"
						filterMode="exclude"
						filterPages="/index"
						grid-gap="12px"
						lg-flex-direction="column"
						lg-padding="6px 0px 6px 0px"
						lg-margin="0px 0px 24px 0px"
						align-items="center"
						flex-wrap="wrap"
						overflow-x="visible"
						overflow-y="visible"
						lg-align-items="flex-start"
					>
						<Override
							slot="link"
							color="--darkL2"
							hover-color="--primary"
							font="--base"
							text-decoration-line="initial"
							transition="color 0.1s ease 0s"
							lg-font="--lead"
						/>
						<Override
							slot="item-active"
							border-width="0 0 2px 0"
							border-style="solid"
							border-color="--color-darkL2"
							lg-border-width="0 0 0 2px"
						/>
						<Override slot="item" padding="6px 6px 6px 6px" lg-padding="6px 12px 6px 12px" />
						<Override slot="link-active" cursor="default" color="--darkL2" hover-color="--darkL2" />
						<Override slot="link-about" />
					</Components.QuarklycommunityKitMenu>
					<Link
						font="--base"
						text-decoration-line="initial"
						margin="0px 0px 0px 16px"
						background="--color-darkL1"
						border-radius="8px"
						href="/complaint"
						color="#ffffff"
						padding="6px 12px 7px 12px"
						lg-margin="0px 0px 24px 0px"
						lg-padding="12px 18px 13px 18px"
						lg-font="--lead"
					>
						Raise a complaint
					</Link>
				</Box>
			</Components.QuarklycommunityKitMobileSidePanel>
		</Section>
		<Section padding="80px 0 80px 0" sm-padding="60px 0 80px 0" quarkly-title="Content-9">
			<Override
				slot="SectionContent"
				padding="0px 0 0px 0px"
				lg-padding="0px 0 0px 0px"
				display="grid"
				grid-template-columns="repeat(2, 1fr)"
				align-items="start"
				lg-grid-template-columns="1fr"
			/>
			<Box
				min-width="100px"
				min-height="100px"
				padding="160px 35px 160px 35px"
				border-color="#b8bcc0"
				margin="0px 80px 0 0"
				lg-margin="40px 0px 35px 0"
				background="linear-gradient(180deg,rgba(10, 2, 22, 0.72) 1.4%,rgba(46, 9, 95, 0.36) 100%) 0 0 no-repeat,rgba(0, 0, 0, 0) url(https://uploads.quarkly.io/65e8b65d926d910020edc60b/images/translucent-blurred-portrait-woman.jpg?v=2024-08-22T16:46:44.996Z) 50% 50% /cover repeat scroll padding-box"
				border-radius="25px"
				sm-padding="50px 25px 30px 25px"
			>
				<Text margin="0px 0px 0px 0px" font="normal 400 22px/1.5 --fontFamily-sans" color="--light">
					In light of the Kolkata Doctor Case, we've initiated Project Vigil 1602 to provide a platform for raising your voice and driving real change.
				</Text>
				<Box
					min-width="10px"
					min-height="10px"
					padding="30px 5px 30px 5px"
					display="flex"
					align-items="center"
				>
					<Text margin="0px 0px 0px 0px" font="normal 300 18px/1.5 --fontFamily-sans" color="--light">
						<Span
							font="400 18px/27px -apple-system, system-ui, &quot;Segoe UI&quot;, Roboto, Oxygen, Ubuntu, Cantarell, &quot;Fira Sans&quot;, &quot;Droid Sans&quot;, &quot;Helvetica Neue&quot;, sans-serif"
							overflow-wrap="normal"
							word-break="normal"
							white-space="normal"
							text-indent="0"
							text-overflow="clip"
							hyphens="manual"
							user-select="auto"
							pointer-events="auto"
						>
							Project Vigil 1602
						</Span>
						{" "}- Vasavi College of Engineering
					</Text>
				</Box>
			</Box>
			<Box min-width="100px" min-height="100px">
				<Text margin="0px 0px 30px 0px" font="normal 600 42px/1.2 --fontFamily-sans">
					Our Goal
				</Text>
				<Text margin="0px 0px 25px 0px" font="normal 300 16px/1.5 --fontFamily-sans" color="#505257">
					We, students of Vasavi College of Engineering, are committed to providing a fully anonymous complaint and feedback portal for the students of Vasavi College of Engineering. This platform empowers students to confidentially raise concerns about issues such as body-shaming, discrimination, inappropriate behavior, misconduct, ragging, and other uncomfortable situations, ensuring their voices are heard and addressed.
				</Text>
				<Text margin="0px 0px 25px 0px" font="normal 300 18px/1.5 --fontFamily-sans" color="#505257">
					At the end of each month, a consolidated list of all complaints will be forwarded to the management of Vasavi College of Engineering. We ensure that complete anonymity is maintained, and all complaints and feedback receive the attention they deserve.
				</Text>
				<Text margin="0px 0px 35px 0px" font="normal 300 18px/1.5 --fontFamily-sans" color="#505257">
					We kindly request that you submit only genuine complaints and feedback. To maintain respect and privacy, please refrain from mentioning any names or identities. We do not condone any form of public disrespect. Misuse of this platform undermines the purpose of addressing real issues and may harm those in need of support. We ask that you use this platform responsibly.
				</Text>
			</Box>
		</Section>
		<Section padding="80px 0 0 0" lg-padding="60px 0 60px 0" md-padding="40px 0 40px 0" sm-padding="30px 0 30px 0">
			<Box margin="0px 0px 32px 0px" display="flex" flex-direction="column" align-items="flex-start">
				<Text
					font="--headline1"
					color="--dark"
					text-align="center"
					md-font="normal 700 42px/1.2 &quot;Source Sans Pro&quot;, sans-serif"
					margin="16px 0px 0px 0px"
				>
					Voices of students
				</Text>
				<Text font="--lead" color="--darkL2">
					All complaints and feedback will be made public while ensuring anonymity to maintain transparency.
				</Text>
			</Box>
		</Section>
		<Section
			id="public-complaints"
			padding="20px 0 80px 0"
			lg-padding="60px 0 60px 0"
			md-padding="40px 0 40px 0"
			sm-padding="30px 0 30px 0"
		>
			
			{complaints.length === 0 ? (
				<Box
				min-width="10px"
				min-height="10px"
				padding="0px 5px 30px 5px"
				display="flex"
				align-items="center"
			>
				<Text margin="0px 0px 0px 0px" font="normal 300 18px/1.5 --fontFamily-sans" color="--dark">
					No complaint yet. Be the first to raise a concern.
				</Text>
			</Box>
			) : (
				complaints.map((complaint) => (
					<Components.QuarklycommunityKitPopup key={complaint.index}>
						<Override
							slot="Button Open"
							font="--base"
							text-decoration-line="initial"
							margin="0px 0px 0px 16px"
							background="--light"
							border-radius="8px"
							href="#"
							color="#000000"
							padding="6px 12px 7px 12px"
							lg-margin="0px 0px 24px 0px"
							lg-padding="12px 18px 13px 18px"
							lg-font="--lead"
						>
							<Strong>
								{complaint.index}. {complaint.subjectLine}
							</Strong>
						</Override>
						<Box
							min-width="100px"
							min-height="100px"
							padding="35px 35px 35px 35px"
							border-color="#b8bcc0"
							margin="0px 80px 0 80px"
							lg-margin="40px 0px 40px 0"
							border-radius="25px"
							sm-padding="50px 25px 50px 25px"
						>
							<Text margin="0px 0px 0px 0px" font="normal 400 22px/1.5 --fontFamily-sans" color="--dark">
								<Strong>
									{complaint.subjectLine}
								</Strong>
							</Text>
							<Box
								min-width="10px"
								min-height="10px"
								padding="30px 5px 30px 5px"
								display="flex"
								align-items="center"
							>
								<Text margin="0px 0px 0px 0px" font="normal 300 18px/1.5 --fontFamily-sans" color="--dark">
									{complaint.body}
								</Text>
							</Box>
							<Box
								min-width="10px"
								min-height="10px"
								padding="10px 5px 10px 5px"
								display="flex"
								align-items="center"
							>
								<Text margin="0px 10px 0px 10px" font="normal 300 18px/1.5 --fontFamily-sans" color="--dark">
									<Strong>
										ID:{" "}
									</Strong>
									{complaint.id} 
								</Text>
								<Text margin="0px 10px 0px 10px" font="normal 300 18px/1.5 --fontFamily-sans" color="--dark">
									<Strong> 
										Timestamp:{" "}
									</Strong>
									{complaint.timestamp}
								</Text>
							</Box>
						</Box>
					</Components.QuarklycommunityKitPopup>
				))
			)}

    
		</Section>
		<RawHtml>
			<style place={"endOfHead"} rawKey={"65e8b65d926d910020edc609"}>
				{":root {\n  box-sizing: border-box;\n}\n\n* {\n  box-sizing: inherit;\n}"}
			</style>
		</RawHtml>
	</Theme>;
});